.nav-bar-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8vw;

  margin: 5vw;

  font-family: header1;
  font-size: 2vw;
}
.nav-bar-wrapper a {
  background-color: rgba(0, 0, 0, 0.5);
  border: 2px solid black;
  border-radius: 16px;
  padding: 8px;

  text-align: center;
  text-decoration: none;
  color: rgb(202, 202, 165);
  transition: 0.2s;
}

.nav-bar-wrapper a:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.nav-bar-wrapper .selected {
  background-color: rgba(0, 0, 0, 0.9);
  color: var(--color5);
}
