.portfolio-wrapper {
  animation: entering 2s;
  max-width: 80vw;
  text-align: center;
}
.portfolio-wrapper ul {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2vw;
  list-style: none;
  background: var(--color6);
  padding: 32px;
  border-radius: 16px;
  border: 2px solid black;
}

.portfolio-wrapper ul li {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-items: center;

  padding: 16px;
  background-color: rgba(109, 145, 175, 0.7);
  border: 8px ridge rgba(109, 145, 175, 0.7);
  border-radius: 4px;

  font-size: 1.2vw;
  transition: 0.5s;
}
.portfolio-wrapper ul li article {
  margin: 32px;
}
.portfolio-wrapper ul li h3 {
  font-family: header1;
  font-size: 2vw;
  letter-spacing: 2px;
}

.iframe-listan {
  width: 375px;
  height: 667px;
}
.img {
  width: 30vw;
}
.portfolio-wrapper li a {
  color: var(--color3);
  padding: 8px 16px;
  background-color: var(--color1);
  border: 2px solid var(--color4);
  border-radius: 8px;
}
.portfolio-wrapper li:hover {
  background-color: var(--color1);
  color: var(--color4);
}
.portfolio-wrapper li a:hover {
  color: white;
  background-color: var(--color2);
}

@media only screen and (max-width: 800px) {
  .portfolio-wrapper ul li {
    grid-template-columns: repeat(1, 1fr);
  }
}
