@keyframes entering {
  from {
    transform: scale(0, 0);
  }
  to {
    transform: scale(1, 1);
  }
}
@keyframes endAnimation {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(0, 0);
  }
}

@keyframes welcomeAnimation {
  0% {
    color: transparent;
    background-color: transparent;
  }
  50% {
    color: black;
    background-color: var(--color2);
    border-radius: 100vw;
    border: 2px solid var(--color3);
    padding: 1vw 5vw;
  }
  100% {
    color: transparent;
    background-color: transparent;
  }
}
