@font-face {
  font-family: header1;
  src: url("./assets/fonts/IMFellEnglishSC-Regular.ttf");
}
@font-face {
  font-family: text1;
  src: url("./assets/fonts/Handlee-Regular.ttf");
}
:root {
  --color1: rgba(0, 0, 0, 0.7);
  --color2: rgba(0, 0, 0, 0.5);
  --color3: rgba(255, 255, 255, 0.7);
  --color4: rgba(255, 255, 255, 0.5);
  --color5: rgb(235, 235, 221);
  --color6: rgba(235, 235, 221, 0.7);
}

* {
  margin: 0;
  padding: 0;
}

.App {
  background: url("./assets/img/background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.main-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 100%;
}

h1 {
  font-family: header1;
  font-weight: 100;
  font-size: 5vw;
  text-align: center;
  padding-top: 4vw;
  color: var(--color1);
  text-shadow: 4px 4px 4px var(--color2);
}

a {
  text-decoration: none;
}
