.about-me-wrapper {
  padding: 32px;
  border: 16px solid rgba(75, 154, 168, 0.3);
  border-left-color: rgba(75, 154, 168, 0.15);
  border-right-color: rgba(75, 154, 168, 0.15);
  background-color: var(--color2);
  color: var(--color3);
  text-align: center;
  animation: entering 2.5s;
  display: flex;
  flex-direction: column;
  width: 60vw;
}

.about-me-wrapper h3 {
  font-family: text1;
  font-weight: 500;
  font-size: 1.5vw;
  letter-spacing: 1px;
  padding: 8px;
  margin: 8px;
  background: var(--color1);
  border-radius: 8px;

  transform: scale(0, 0);
  animation: entering 2s;
  animation-fill-mode: forwards;
}
