.cv-wrapper {
  width: 60vw;
  display: flex;
  flex-direction: column;
  gap: 16px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.2vw;
  font-weight: 500;
}
.small-info {
  padding: 16px;
  border: 4px ridge black;
  border-radius: 8px;
  background: var(--color4);
  width: 40vw;
  align-self: center;
  animation: entering 2s;
}
.small-info h2 {
  text-align: center;
}
.small-info-work {
  padding: 16px;
  border: 4px ridge black;
  border-radius: 8px;
  background-color: var(--color4);
  transition: all 0.5s;
  animation: entering 2s;
}
.small-info-work h2 {
  text-align: center;
}
.education {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.education h2 {
  grid-column: 1/3;
}
.education ul {
  list-style: none;
  text-align: center;
}
.education li {
  margin: 4px;
  background-color: rgba(5, 141, 39, 0.541);
}
.small-info-work h2 {
  margin: 8px 8px 16px 8px;
}
.small-info-work div {
  margin: 8px;
}

.entering {
  animation: entering 2s;
}
